body {
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
}

.contenedor {
  width: 95%;
  max-width: 600px;
  margin: 0 auto;
}

a {
  font-family: "Varela Round", sans-serif;
  text-decoration: none;
}

p {
  font-family: "Varela Round", sans-serif;
}

img {
  max-width: 100%;
}

h1, h2, h3, h4 {
  font-family: "Roboto", sans-serif;
  margin: 0;
  color: #202124;
  text-align: center;
}

h1 {
  font-size: 3.5rem;
  font-weight: 900;
}

h2 {
  font-size: 2.8rem;
  font-weight: 700;
}

h3 {
  font-size: 2rem;
}

h4 {
  font-size: 1.7rem;
}

p {
  font-size: 1.4rem;
  margin: 0;
}

body {
  position: relative;
}

.menu-lateral {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 1;
  background-color: rgba(28, 29, 32, 0.81);
  width: 0rem;
  height: 64vh;
  transition: width 1s ease;
  overflow: hidden;
  text-align: right;
  padding-top: 2rem;
  margin-top: 7rem;
  border-top-left-radius: 1.2rem;
  border-bottom-left-radius: 1.2rem;
}
.menu-lateral label {
  color: #fafafa;
  font-family: "Roboto", sans-serif;
  padding: 1rem 2rem;
  background-color: rgba(255, 254, 254, 0.1215686275);
  border-radius: 20%;
  font-size: 2rem;
  position: absolute;
  top: 0;
  left: 0;
  margin: 1rem;
}
.menu-lateral label:hover {
  cursor: pointer;
  background-color: #eb203f;
  border-radius: 20%;
  font-weight: 700;
}
.menu-lateral p {
  padding: 1rem 1rem 2rem 1rem;
  color: #fafafa;
  text-transform: capitalize;
  font-family: "Roboto", sans-serif;
}
.menu-lateral p:hover {
  cursor: pointer;
  color: #eb203f;
  font-weight: 700;
}
.menu-lateral ul {
  margin-top: 3rem;
  list-style: none;
  padding: 0rem;
}
.menu-lateral ul li {
  white-space: nowrap;
  padding: 0.5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.menu-lateral ul li img {
  width: 15%;
}

.mostrar_menuL {
  width: 70%;
  margin-top: 5rem;
}
@media (min-width: 430px) {
  .mostrar_menuL {
    height: 40%;
  }
}
@media (min-width: 600px) {
  .mostrar_menuL {
    display: none;
  }
}

.carrito-compras {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  background-color: #fafafa;
  width: 100%;
  height: 100%;
  padding-top: 2rem;
  display: none;
}
.carrito-compras .tu-pedido {
  display: flex;
  justify-content: space-between;
  align-items: center;
  text-transform: uppercase;
  border-bottom: 1px solid #dedddd;
  padding-bottom: 1rem;
}
.carrito-compras .tu-pedido label {
  color: #202124;
  font-size: 3rem;
  font-weight: 700;
  background-color: #dedddd;
  border-radius: 50%;
  padding: 0.1rem 1rem;
}
.carrito-compras .tu-pedido label:hover {
  cursor: pointer;
}
.carrito-compras .carrito {
  text-align: center;
}
.carrito-compras .carrito .carrito-table {
  width: 100%;
  color: #202124;
  border-collapse: collapse;
  margin: 2rem 0;
}
.carrito-compras .carrito .carrito-table thead {
  background-color: #797676;
}
.carrito-compras .carrito .carrito-table thead th {
  padding: 1rem 0.2rem;
  color: #fafafa;
}
.carrito-compras .carrito .carrito-table tbody td {
  padding: 1rem 0;
  border-bottom: 1px solid #dedddd;
  color: #797676;
}
.carrito-compras .carrito .carrito-table tbody .borrar-producto {
  color: #fafafa;
  font-weight: 700;
  background-color: #eb203f;
  padding: 0.4rem 0.7rem;
  border-radius: 50%;
}
.carrito-compras .carrito .valor-total {
  font-size: 1.7rem;
  color: #797676;
  padding: 1rem;
}
.carrito-compras .carrito .valor-total span {
  font-size: 2rem;
  font-weight: 700;
  color: #202124;
  padding: 1rem;
}
.carrito-compras .carrito .carrito-continuar {
  display: inline-block;
  background-color: rgba(50, 159, 0, 0.6784313725);
  color: #fafafa;
  padding: 1.4rem 3rem;
  font-weight: 700;
  border: none;
  border-radius: 0.8rem;
}

.mostrarcarrito {
  display: block;
}

.presencial-domicilio {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
  background-color: #fafafa;
  width: 100%;
  height: 100vh;
  padding: 1rem 0;
  display: none;
}
.presencial-domicilio .atras {
  margin-top: 2rem;
  text-align: left;
}
.presencial-domicilio .atras a {
  color: #797676;
  border: 1px solid #dedddd;
  padding: 1rem 2rem;
  font-size: 2rem;
  border-radius: 0.8rem;
}
.presencial-domicilio .atras a:hover {
  cursor: pointer;
  background-color: #797676;
  color: #fafafa;
}
.presencial-domicilio h3 {
  margin-top: 2rem;
  padding: 2rem;
}
.presencial-domicilio .contenido-p-d {
  margin-top: 3rem;
  text-align: center;
  padding-bottom: 2rem;
  border-bottom: 1px solid #dedddd;
}
.presencial-domicilio .contenido-p-d .carrito-continuar-p {
  display: inline-block;
  background-color: rgba(50, 159, 0, 0.6784313725);
  color: #fafafa;
  padding: 1.4rem 3rem;
  font-weight: 700;
  border: none;
  border-radius: 0.8rem;
  margin-right: 1rem;
}
.presencial-domicilio .contenido-p-d .carrito-continuar-d {
  display: inline-block;
  background-color: #eb203f;
  color: #fafafa;
  padding: 1.4rem 3rem;
  font-weight: 700;
  border: none;
  border-radius: 0.8rem;
}
.presencial-domicilio .carrito-continuar-p .bell, .presencial-domicilio .carrito-continuar-d .bell {
  font-size: 2rem;
  gap: 2rem;
}
.presencial-domicilio .carrito-continuar-p a, .presencial-domicilio .carrito-continuar-d a {
  color: #fafafa;
}

.mostrar-presencial-domicilio {
  display: block;
}

.formulario-presencial {
  display: none;
  text-align: center;
  margin-top: 2rem;
}
.formulario-presencial form h4 {
  color: #202124;
  margin-bottom: 2rem;
}
.formulario-presencial form input {
  margin-bottom: 1rem;
  padding: 1rem;
  border: 1px solid #127dd4;
  color: #797676;
  width: 20%;
}
.formulario-presencial form input:focus {
  border: none;
  outline: 1px solid #062a47;
}
.formulario-presencial form button {
  display: inline-block;
  background-color: rgba(50, 159, 0, 0.6784313725);
  color: #fafafa;
  padding: 1.4rem 3rem;
  font-weight: 700;
  border: none;
  border-radius: 0.8rem;
  margin-top: 2rem;
}
.formulario-presencial .enviar-orden button .bell {
  margin-right: 0.8rem;
}

.formulario-presencial-js {
  display: block;
}

.datos-presencial .form-control {
  display: block;
  width: 100%;
  padding: 0.375rem 0.75rem;
  font-size: 1.9rem;
  line-height: 1.5;
  color: #495057;
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid #ced4da;
  border-radius: 0.25rem;
  transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
  font-family: "Roboto", sans-serif;
}
.datos-presencial textarea {
  min-height: 65px;
  padding-top: 6px;
  padding-bottom: 6px;
}
.datos-presencial input::placeholder {
  font-size: 1.7rem;
}

.formulario-domicilio {
  display: none;
  text-align: center;
  margin-top: 2rem;
}
.formulario-domicilio form h4 {
  color: #202124;
  margin-bottom: 1rem;
}
.formulario-domicilio form input {
  margin-bottom: 1rem;
  padding: 1rem;
  border: 1px solid #127dd4;
  color: #797676;
}
.formulario-domicilio form input:focus {
  border: none;
  outline: 1px solid #062a47;
}
.formulario-domicilio form button {
  display: inline-block;
  background-color: #eb203f;
  color: #fafafa;
  padding: 1.4rem 3rem;
  font-weight: 700;
  border: none;
  border-radius: 0.8rem;
  margin-top: 2rem;
}

.formulario-domicilio-js {
  display: block;
}

body::-webkit-scrollbar {
  width: 7px;
}

body::-webkit-scrollbar-thumb {
  background: #eb203f;
  border-radius: 5px;
}

.svg-superior svg path {
  fill: #eb203f;
}

header {
  min-height: 17rem;
  background-image: url(../img/headere.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  transition: all 0.7s ease;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
header .navegacion {
  display: flex;
  align-items: center;
  padding: 0rem 1rem;
}
header .navegacion .menu-movil {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}
header .navegacion .menu-movil .abrir-menu {
  height: 100%;
}
header .navegacion .menu-movil .abrir-menu i {
  width: 5rem;
  padding: 1rem;
  font-size: 3.2rem;
  position: absolute;
  top: -1px;
  color: #fafafa;
}
header .navegacion .menu-movil .abrir-menu i:hover {
  cursor: pointer;
}
@media (min-width: 600px) {
  header .navegacion .menu-movil .abrir-menu {
    display: none;
  }
}
header .navegacion .menu-movil img {
  width: 5rem;
  padding: 1rem;
}
header .navegacion .menu-movil img:hover {
  cursor: pointer;
}
header .navegacion .menu-movil .img-carrito {
  height: 5.2rem;
  position: absolute;
  top: 2px;
  left: 4px;
}
header .home {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
}
header .home .enlace-home {
  position: absolute;
  top: -20rem;
}
header .home .enlace-home img {
  max-width: 100%;
  width: 14rem;
  margin: auto;
}
header .home .rrss {
  display: flex;
  justify-content: center;
  gap: 1.7rem;
  padding: 1rem 0;
  position: absolute;
  top: -6rem;
}
header .home .rrss a {
  display: flex;
  justify-content: center;
  background-color: #062a47;
  padding: 0.5rem;
  border-radius: 50%;
  width: 3.3rem;
  height: 3.3rem;
}
header .home .rrss a i {
  color: #fafafa;
  font-size: 2rem;
}
header .contenido-header h1, header .contenido-header h2 {
  margin: 2rem 0;
  text-align: left;
  color: #202124;
}
header .contenido-header h1 {
  font-size: 3rem;
}

.reducir-altura {
  min-height: 0rem;
  transition: all 0.4s ease;
}

.barra-categorias-texto h2 {
  font-size: 2.2rem;
  font-weight: 400;
}

