.btn-flotante {
  display: none;
  visibility: hidden;
  position: fixed;
  bottom: 5rem;
  right: 1.8rem;
  z-index: 1000;
  background-color: rgba(50, 159, 0, 0.6784313725);
  border-radius: 50%;
  transition: all 1s ease;
  animation: ws 1.2s infinite;
}
.btn-flotante img {
  width: 6rem;
}
.btn-flotante:hover {
  transform: translate(-25%, -25%);
}

@keyframes ws {
  0% {
    box-shadow: 0 0 0 0 rgba(50, 159, 0, 0.85);
  }
  100% {
    box-shadow: 0 0 0 25px rgba(50, 159, 0, 0.2);
  }
}
.boton-arriba {
  width: 4rem;
  height: 4rem;
  background-color: rgba(86, 86, 86, 0.5215686275);
  border-radius: 50%;
  position: fixed;
  bottom: 13rem;
  right: 2.8rem;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  display: none;
  z-index: 15;
}
.boton-arriba:hover {
  background-color: #797676;
}
.boton-arriba .btn-up {
  color: #fafafa;
  font-size: 2rem;
  font-weight: 400;
  padding: 1rem;
}

@keyframes ws {
  0% {
    box-shadow: 0 0 0 0 rgba(50, 159, 0, 0.9);
  }
  50% {
    box-shadow: 0 0 0 8px rgba(50, 159, 0, 0.7);
  }
  100% {
    box-shadow: 0 0 0 15px rgba(50, 159, 0, 0);
  }
}
.cantidad-ingred {
  border: 1px solid #797676;
  border-radius: 0.8rem;
  padding: 1rem;
}
.cantidad-ingred label {
  margin-right: 2rem;
}
.cantidad-ingred .incremento-ingred, .cantidad-ingred .decremento-ingred {
  border-radius: 50%;
  border: 1px solid #202124;
  padding: 0.2rem 0.7rem;
  background-color: #202124;
  color: #fafafa;
}
.cantidad-ingred .incremento-ingred:hover, .cantidad-ingred .decremento-ingred:hover {
  cursor: pointer;
}
.cantidad-ingred .decremento-ingred {
  font-size: 2rem;
  padding: 0rem 0.9rem;
}
.cantidad-ingred .num-ingred {
  color: #202124;
}
.cantidad-ingred input {
  width: 2rem;
  height: 1.725rem;
  margin-left: 2rem;
}

.alerta {
  margin-top: 3rem;
}
.alerta p {
  text-align: center;
  line-height: 1.5;
  font-size: 1.7rem;
  color: #ef4f67;
}

.sweetbtn {
  font-size: 1.5rem !important;
}

.carrito-inferior {
  display: none;
  background-color: #202124;
  text-align: center;
  padding: 0.4rem;
  position: sticky;
  bottom: 0rem;
  right: 0rem;
  width: 100%;
  z-index: 10;
  margin-bottom: -0.5rem;
}
.carrito-inferior img {
  width: 3.5rem;
}

footer {
  text-align: center;
}
footer .footer-restaurante {
  padding-top: 2rem;
  padding-bottom: 2rem;
  background-color: #202124;
  color: #fafafa;
}
footer .desarrollo {
  background-color: #eb203f;
  color: #fafafa;
  padding: 1.4rem;
}

.home {
  position: relative;
}
.home .img-hero {
  position: absolute;
  top: 50%;
