  font-style: italic;
}

.bloque-rrss {
  padding-top: 2rem;
}
.bloque-rrss .rrss {
  display: flex;
  justify-content: center;
  gap: 1.7rem;
  margin: 0 0.7rem;
}
.bloque-rrss .rrss a i {
  color: #eb203f;
